import { Implementation } from '..';
export class DynamicImportEnvs {
    implementation: Implementation;
    constructor(implementation: Implementation) {
        this.implementation = implementation;
    }
    async AppDrawingBlock(id: string) {
        return import('./AppDrawingBlock').then(({ AppDrawingBlock }) => {
            AppDrawingBlock.Init(id);
        }).catch((err) => { console.log('Stop loading Apps.'); console.log(err); });
    }
    async AppEnvCanvas(id: string, subApp?: string) {
        return import('./AppCanvasEnv').then(({ AppCanvasEnv }) => {
            if (subApp) {
                AppCanvasEnv.InitSubApp(id, subApp);
            } else {
                AppCanvasEnv.Init(id);
            }
        }).catch((err) => {
            console.log('Stop loading Apps.'); console.log(err);
        });
    }
    async AppEnvThree(id: string) {
        return import('./AppThreeEnv').then(({ AppThreeEnv }) => {
            AppThreeEnv.Init(id);
        }).catch((err) => {
            console.log('Stop loading Apps.'); console.log(err);
        });
    }
    async AppEnvGoogleMap(id: string) {
        console.log('ddd');
        return import('./AppGoogleMapEnv').then(({ AppGoogleMapEnv }) => {
            AppGoogleMapEnv.Init(id);
        }).catch((err) => {
            console.log('Stop loading Apps.'); console.log(err);
        });
    }
    async AppEnvAlgorithm(id: string, subApp?: string) {
        return import('./AppAlgorithmEnv').then(({ AppAlgorithmEnv }) => {
            if (subApp) {
                AppAlgorithmEnv.InitSubApp(id, subApp);
            } else {
                AppAlgorithmEnv.Init(id);
            }

        }).catch((err) => {
            console.log('Stop loading Apps.'); console.log(err);
        });
    }
    // .....................................................................
    async DemoAlgorithm01(id: string, subApp: string) {
        return import('./AppCanvasEnv').then(({ AppCanvasEnv }) => {
            AppCanvasEnv.InitSubApp(id, subApp);
        }).catch((err) => { console.log('Stop loading Apps.'); console.log(err); });
    }
}